@layer components {
  .hamburger {
    cursor: pointer;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 20px;
    transition-property: opacity;
    transition-duration: .15s;
    transition-timing-function: linear;
    display: flex;
  }

  .hamburger .hamburger-box {
    width: 32px;
    height: 20px;
    display: inline-block;
    position: relative;
  }

  .hamburger .hamburger-inner {
    background-color: var(--color-primary);
    border-radius: 4px;
    width: 32px;
    height: 4px;
    margin-top: -2px;
    transition-duration: 75ms;
    transition-timing-function: cubic-bezier(.55, .055, .675, .19);
    display: block;
    position: absolute;
    top: 50%;
  }

  .hamburger .hamburger-inner:before {
    background-color: inherit;
    content: "";
    border-radius: 4px;
    width: 32px;
    height: 4px;
    transition: top 75ms .12s, opacity 75ms;
    display: block;
    position: absolute;
    top: -8px;
  }

  .hamburger .hamburger-inner:after {
    background-color: inherit;
    content: "";
    border-radius: 4px;
    width: 32px;
    height: 4px;
    transition: bottom 75ms .12s, transform 75ms cubic-bezier(.55, .055, .675, .19);
    display: block;
    position: absolute;
    bottom: -8px;
  }

  .hamburger.active .hamburger-inner {
    transition-delay: .12s;
    transition-timing-function: cubic-bezier(.215, .61, .355, 1);
    transform: rotate(45deg);
  }

  .hamburger.active .hamburger-inner:before {
    opacity: 0;
    transition: top 75ms, opacity 75ms .12s;
    top: 0;
  }

  .hamburger.active .hamburger-inner:after {
    transition: bottom 75ms, transform 75ms cubic-bezier(.215, .61, .355, 1) .12s;
    bottom: 0;
    transform: rotate(-90deg);
  }
}

